<template>
  <div class="token-filters">
    <div class="filter-tabs">
      <button
        v-for="tab in tabs"
        :key="tab.key"
        class="filter-tab"
        :class="{ 'filter-tab--active': activeTab === tab.key }"
        @click="handleTabClick(tab.key)"
      >
        <div class="tab-content">
          <span class="tab-icon-container">
            <i :class="tab.icon"></i>
            <span v-if="tab.count !== undefined" class="tab-count">{{ tab.count }}</span>
          </span>
          <span class="tab-name">{{ tab.name }}</span>
        </div>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TokenFilters',
  props: {
    activeTab: {
      type: String,
      default: 'all'
    },
    tokenCounts: {
      type: Object,
      default: () => ({
        all: 0,
        active: 0,
        minting: 0,
        launched: 0,
        locked: 0
      })
    }
  },
  emits: ['tab-change'],
  computed: {
    tabs() {
      return [
        {
          key: 'all',
          name: 'All Tokens',
          icon: 'fas fa-coins',
          count: this.tokenCounts.all
        },
        {
          key: 'active',
          name: 'Active Squad',
          icon: 'fas fa-users',
          count: this.tokenCounts.active
        },
        {
          key: 'minting',
          name: 'Minting',
          icon: 'fas fa-hammer',
          count: this.tokenCounts.minting
        },
        {
          key: 'launched',
          name: 'Launched',
          icon: 'fas fa-rocket',
          count: this.tokenCounts.launched
        },
        {
          key: 'locked',
          name: 'Locked',
          icon: 'fas fa-lock',
          count: this.tokenCounts.locked
        }
      ]
    }
  },
  methods: {
    handleTabClick(tabKey) {
      this.$emit('tab-change', tabKey)
    }
  }
}
</script>

