  gap: 4px;
}

.tab-icon-container {
  display: flex;
  align-items: center;
  gap: 5px;
}

.tab-name {
  font-size: 0.875rem;
  font-weight: 600;
}

.tab-count {
  font-size: 0.85rem;
  /* opacity: 0.8; */
  background: rgba(255, 255, 255, 0.2);
  padding: 2px 8px;
  border-radius: 10px;
  min-width: 20px;
  text-align: center;
}

.filter-tab:not(.filter-tab--active) .tab-count {
  background: rgba(0, 0, 0, 0.1);
  color: #666;
}

.filter-tab i {
  font-size: 1.2rem;
  margin-bottom: 4px;
}

.filter-tab:not(.filter-tab--active) {
  color: #666;
}

