<template>
  <div class="token-card" @click="$emit('click')">
    <div class="token-header">
      <div class="token-logo">
        <img 
          v-if="token.logoUrl" 
          :src="token.logoUrl" 
          :alt="token.name"
          @error="handleImageError"
        />
        <div v-else class="token-logo-placeholder">
          <i class="fas fa-coins"></i>
        </div>
      </div>
      
      <div class="token-info">
        <h3 class="token-name">{{ token.name }}</h3>
        <span class="token-symbol">${{ token.symbol }}</span>
      </div>
      
      <div class="token-status">
        <span class="status-badge" :class="`status-badge--${getStatus()}`">
          <i :class="getStatusIcon()"></i>
          {{ getStatusText() }}
        </span>
      </div>
    </div>

    <div class="token-description">
      <p>{{ truncatedDescription }}</p>
    </div>

    <div v-if="showSquadProgress" class="squad-progress">
      <div class="progress-header">
        <span class="progress-label">Squad Progress</span>
        <span class="progress-percentage">{{ Math.round(token.squadProgress) }}%</span>
      </div>
      <div class="progress-bar">
        <div 
          class="progress-fill" 
          :style="{ width: `${Math.min(token.squadProgress, 100)}%` }"
        ></div>
      </div>
      <div class="progress-info">
        <span class="progress-current">{{ formatLamports(token.currentSquadLamports) }} SOL</span>
        <span class="progress-target">{{ formatLamports(token.maxSquadLamports) }} SOL</span>
      </div>
      <div v-if="token.minSquadLamports > 0" class="min-squad-info">
        <div class="min-squad-label">
          <i class="fas fa-rocket"></i>
          <span>Minimum for launch: {{ formatLamports(token.minSquadLamports) }} SOL</span>
        </div>
        <span v-if="isMinReached()" class="min-reached">✓ Ready to launch</span>
        <span v-else class="min-not-reached">
          {{ formatLamports(token.minSquadLamports - token.currentSquadLamports) }} SOL to go
        </span>
      </div>
    </div>

    <div class="token-stats">
      <!-- <div class="stat-item">
        <i class="fas fa-users"></i>
        <span>{{ getSquadCount() }}</span>
      </div> -->

      <div class="stat-item">
        <i class="fas fa-user"></i>
        <span>{{ token.userTag }}</span>
      </div>
      <div v-if="token.createdAt" class="stat-item">
        <i class="fas fa-calendar"></i>
        <span>{{ formatDate(token.createdAt) }}</span>
      </div>

      <div class="stat-item">
        <i class="fas fa-heart"></i>
        <span>{{ token.likes || 0 }}</span>
      </div>
    </div>

    <div v-if="getTimeInfo()" class="token-timing">
      <i :class="getTimeIcon()"></i>
      <span>{{ getTimeInfo() }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TokenCard',
  props: {
    token: {
      type: Object,
      required: true
    }
  },
  emits: ['click'],
  computed: {
    truncatedDescription() {
      if (!this.token.description) return 'No description available'
      return this.token.description.length > 120 
        ? this.token.description.substring(0, 120) + '...'
        : this.token.description
    },
    
    showSquadProgress() {
      return this.getStatus() === 'active' && this.token.maxSquadLamports > 0
    }
  },
  methods: {
    getStatus() {
      if (this.token.mint && this.token.launchTx) {
        const now = new Date()
        if (this.token.unlockTime && new Date(this.token.unlockTime) > now) {
          return 'locked'
        }
        return 'launched'
      } else if (this.token.minting || this.token.squadCompleted) {
        return 'minting'
      }
      return 'active'
    },

    getStatusText() {
      const statusMap = {
        active: 'Collecting Squad',
        minting: 'Preparing Launch',
        launched: 'Launched',
        locked: 'Liquidity Locked'
      }
      return statusMap[this.getStatus()] || 'Unknown'
    },

    getStatusIcon() {
      const iconMap = {
        active: 'fas fa-users',
        minting: 'fas fa-hammer',
        launched: 'fas fa-rocket',
        locked: 'fas fa-lock'
      }
      return iconMap[this.getStatus()] || 'fas fa-question'
    },

    // getSquadCount() {
    //   // Примерный расчет участников squad на основе собранных средств
    //   if (!this.token.currentSquadLamports || !this.token.maxSquadLamports) return '0'
    //   const avgContribution = this.token.maxSquadLamports / 10 // Условно 10 участников на максимум
    //   return Math.max(1, Math.floor(this.token.currentSquadLamports / avgContribution))
    // },

    getTimeInfo() {
      const status = this.getStatus()
      const now = new Date()
      
      if (status === 'locked' && this.token.unlockTime) {
        const unlockDate = new Date(this.token.unlockTime)
        const timeUntilUnlock = this.formatTimeUntil(unlockDate)
        return `Unlocks: ${this.formatDate(this.token.unlockTime)} (${timeUntilUnlock})`
      } else if (status === 'launched') {
        if (this.token.unlockedAt) {
          // Показываем информацию о разблокировке
          const timeSinceUnlock = this.formatTimeSince(this.token.unlockedAt)
          return `Unlocked: ${this.formatDate(this.token.unlockedAt)} (${timeSinceUnlock} ago)`
        } else if (this.token.launchTime) {
          // Показываем информацию о запуске
          return `Launched: ${this.formatDate(this.token.launchTime)}`
        }
      }
      return null
    },

    getTimeIcon() {
      const status = this.getStatus()
      return status === 'locked' ? 'fas fa-clock' : 'fas fa-rocket'
    },

    formatLamports(lamports) {
      if (!lamports) return '0'
      return (lamports / 1e9).toFixed(2)
    },

    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', { 
        month: 'short', 
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },

    formatSimpleDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', { 
        month: 'short', 
        day: 'numeric'
      });
    },

    formatTimeUntil(targetDate) {
      const now = new Date()
      const diffMs = targetDate.getTime() - now.getTime()
      const diffDays = Math.ceil(diffMs / (1000 * 60 * 60 * 24))
      const diffHours = Math.ceil(diffMs / (1000 * 60 * 60))
      
      if (diffDays > 1) {
        return `${diffDays}d`
      } else if (diffHours > 1) {
        return `${diffHours}h`
      } else {
        const diffMinutes = Math.ceil(diffMs / (1000 * 60))
        return diffMinutes > 0 ? `${diffMinutes}m` : 'soon'
      }
    },

    formatTimeSince(dateString) {
      const date = new Date(dateString)
      const now = new Date()
      const diffMs = now.getTime() - date.getTime()
      const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24))
      const diffHours = Math.floor(diffMs / (1000 * 60 * 60))
      
      if (diffDays > 0) {
        return `${diffDays}d`
      } else if (diffHours > 0) {
        return `${diffHours}h`
      } else {
        const diffMinutes = Math.floor(diffMs / (1000 * 60))
        return diffMinutes > 0 ? `${diffMinutes}m` : 'just now'
      }
    },

    formatRelativeTime(dateString) {
      const date = new Date(dateString)
      const now = new Date()
      const diffMs = date.getTime() - now.getTime()
      const diffDays = Math.ceil(diffMs / (1000 * 60 * 60 * 24))
      
      if (diffDays > 0) {
        return `in ${diffDays}d`
      } else if (diffDays === 0) {
        return 'today'
      } else {
        return `${Math.abs(diffDays)}d ago`
      }
    },

    handleImageError(event) {
      event.target.style.display = 'none'
      event.target.nextElementSibling?.classList.remove('hidden')
    },

    isMinReached() {
      return this.token.currentSquadLamports >= this.token.minSquadLamports
    }
  }
}
</script>

