<template>
  <div class="tokens-page">
    <div class="tokens-container">
      <div class="tokens-header">
        <h1 class="tokens-title">Token Launches</h1>
        <p class="tokens-subtitle">
          Discover and track all BITCAT token launches
        </p>
        
        <div class="tokens-actions">
          <router-link to="/token-launch" class="action-button action-button--primary">
            <i class="fas fa-plus"></i>
            Create Token
          </router-link>
          <router-link to="/token-launch-info" class="action-button action-button--secondary">
            <i class="fas fa-info-circle"></i>
            How it works
          </router-link>
        </div>
        
        <!-- <div class="tokens-stats">
          <div class="stat-item">
            <span class="stat-value">{{ totalTokens }}</span>
            <span class="stat-label">Total Tokens</span>
          </div>
          <div class="stat-item">
            <span class="stat-value">{{ activeTokens }}</span>
            <span class="stat-label">Active Squads</span>
          </div>
          <div class="stat-item">
            <span class="stat-value">{{ launchedTokens }}</span>
            <span class="stat-label">Launched</span>
          </div>
        </div> -->
      </div>

      <TokenFilters 
        :activeTab="activeTab"
        :tokenCounts="tokenCounts"
        @tab-change="handleTabChange"
      />

      <div class="tokens-content">
        <TransitionGroup name="token-list" tag="div" class="tokens-grid">
          <TokenCard
            v-for="token in filteredTokens"
            :key="token.tokenId"
            :token="token"
            @click="goToToken(token.tokenId)"
          />
        </TransitionGroup>

        <div v-if="isLoading && tokens.length === 0" class="loading-state">
          <div class="loading-spinner"></div>
          <p>Loading tokens...</p>
        </div>

        <div v-if="!isLoading && filteredTokens.length === 0" class="empty-state">
          <div class="empty-icon">
            <i class="fas fa-coins"></i>
          </div>
          <h3>No tokens found</h3>
          <p>{{ getEmptyStateMessage() }}</p>
        </div>
      </div>

      <div v-if="lastUpdated" class="update-indicator">
        <i class="fas fa-sync" :class="{ 'fa-spin': isLoading }"></i>
        <span>Last updated: {{ formatTime(lastUpdated) }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'
import TokenCard from '@/components/tokens/TokenCard.vue'
import TokenFilters from '@/components/tokens/TokenFilters.vue'
import axios from 'axios'

export default {
  name: 'TokensPage',
  components: {
    TokenCard,
    TokenFilters
  },
  setup() {
    const router = useRouter()
    const tokens = ref({
      active: [],
      minting: [],
      launched: [],
      locked: []
    })
    const isLoading = ref(false)
    const lastUpdated = ref(null)
    const activeTab = ref('all')
    const refreshInterval = ref(null)

    const totalTokens = computed(() => {
      return Object.values(tokens.value).reduce((sum, arr) => sum + arr.length, 0)
    })

    const activeTokens = computed(() => tokens.value.active.length)
    const launchedTokens = computed(() => tokens.value.launched.length)

    const tokenCounts = computed(() => ({
      all: totalTokens.value,
      active: tokens.value.active.length,
      minting: tokens.value.minting.length,
      launched: tokens.value.launched.length,
      locked: tokens.value.locked.length
    }))

    const filteredTokens = computed(() => {
      if (activeTab.value === 'all') {
        return [
          ...tokens.value.active,
          ...tokens.value.minting,
          ...tokens.value.launched,
          ...tokens.value.locked
        ]
      }
      return tokens.value[activeTab.value] || []
    })

    const fetchTokens = async (showLoading = true) => {
      if (showLoading) {
        isLoading.value = true
      }

      try {
        const response = await axios.get('/tokens')
        const data = response.data

        if (data.success) {
          // Плавное обновление без мерцания
          const prevTokenIds = new Set(
            Object.values(tokens.value).flat().map(t => t.tokenId)
          )
          
          const newData = data.data
          const newTokenIds = new Set(
            Object.values(newData).flat().map(t => t.tokenId)
          )

          // Если состав токенов изменился, обновляем
          if (prevTokenIds.size !== newTokenIds.size || 
              ![...prevTokenIds].every(id => newTokenIds.has(id))) {
            tokens.value = newData
          } else {
            // Обновляем только данные существующих токенов
            Object.keys(newData).forEach(status => {
              tokens.value[status].forEach((token, index) => {
                const newToken = newData[status].find(t => t.tokenId === token.tokenId)
                if (newToken) {
                  Object.assign(tokens.value[status][index], newToken)
                }
              })
            })
          }

          lastUpdated.value = new Date()
        }
      } catch (error) {
        console.error('Error fetching tokens:', error)
      } finally {
        isLoading.value = false
      }
    }

    const handleTabChange = (tab) => {
      activeTab.value = tab
    }

    const goToToken = (tokenId) => {
      router.push(`/token/${tokenId}`)
    }

    const getEmptyStateMessage = () => {
      const messages = {
        all: 'No tokens have been created yet.',
        active: 'No tokens are currently collecting squad members.',
        minting: 'No tokens are currently being minted.',
        launched: 'No tokens have been launched yet.',
        locked: 'No tokens have locked liquidity currently.'
      }
      return messages[activeTab.value] || 'No tokens found.'
    }

    const formatTime = (date) => {
      return new Intl.DateTimeFormat('en-US', {
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      }).format(date)
    }

    const startAutoRefresh = () => {
      refreshInterval.value = setInterval(() => {
        fetchTokens(false) // Обновляем без показа загрузки
      }, 10000) // Каждые 10 секунд
    }

    const stopAutoRefresh = () => {
      if (refreshInterval.value) {
        clearInterval(refreshInterval.value)
        refreshInterval.value = null
      }
    }

    onMounted(() => {
      fetchTokens()
      startAutoRefresh()
    })

    onUnmounted(() => {
      stopAutoRefresh()
    })

    return {
      tokens,
      isLoading,
      lastUpdated,
      activeTab,
      totalTokens,
      activeTokens,
      launchedTokens,
      tokenCounts,
      filteredTokens,
      handleTabChange,
      goToToken,
      getEmptyStateMessage,
      formatTime
    }
  }
}
</script>

